<template>
  <div style="width:100%;">
    <div id="container"></div>
    <div id="panel"></div>
  </div>
</template>

<script>
// const user = require('./../assets/myself.png')
// const rider = require('./../assets/rider.png')
// const store = require('./../assets/store.png')
const user = ''
const rider = ''
const store = ''
export default {
    data () {
      return {
        
      }
    },
    mounted() {
      this.$stamp(null,this.$route.params.orderId)
      this.$stamp(null,this.$route.params.userLocation)
      this.$store.state.loading = true
      this.getLoaction(this.$route.params.orderId).then(result => {
        this.$stamp(null,result)
        if(result.data.data == null){
          this.$router.push('/manager/orders')
          this.$message.error(result.data.msg+',无法查看地图!')
        }else{
          let center = [result.data.data.lng,result.data.data.lat]
          var storeMarker,redingMarker,userMarker, map = new AMap.Map("container", {
              resizeEnable: true,
              center: center,//骑手地图中心点
              zoom: 13 //地图显示的缩放级别
          });
          // 用户坐标
          var endIcon = new AMap.Icon({
              size: new AMap.Size(45, 45),
              image: user,
              imageSize: new AMap.Size(45, 45),
              imageOffset: new AMap.Pixel(0, 0)
          });
          userMarker = new AMap.Marker({
              icon: endIcon,
              position: this.$route.params.userLocation,
              offset: new AMap.Pixel(-15, -45)
          });
          userMarker.setMap(map);
          //骑手坐标
          var startIcon = new AMap.Icon({
              size: new AMap.Size(45, 45),
              image: rider,
              imageSize: new AMap.Size(45, 45),
              imageOffset: new AMap.Pixel(0, 0)
          });
          redingMarker = new AMap.Marker({
              icon: startIcon,
              position: center,
              offset: new AMap.Pixel(-23, -45)
          });
          redingMarker.setMap(map);
          //门店坐标
          var storeIcon = new AMap.Icon({
              size: new AMap.Size(45, 45),
              image: store,
              imageSize: new AMap.Size(45, 45),
              imageOffset: new AMap.Pixel(0, 0)
          });
          storeMarker = new AMap.Marker({
              icon: storeIcon,
              position: [this.$store.getters.storesMessage.lng,this.$store.getters.storesMessage.lat],
              offset: new AMap.Pixel(-13, -30)
          });
          storeMarker.setMap(map);
          var riding = new AMap.Riding({
                map: map,
                panel: "panel",
                hideMarkers:true,
                autoFitView:false
              }); 
          //根据起终点坐标规划骑行路线
          riding.search(center,this.$route.params.userLocation, (status, result) => {
              // result即是对应的骑行路线数据信息，相关数据结构文档请参考  https://lbs.amap.com/api/javascript-api/reference/route-search#m_RidingResult
              if (status === 'complete') {
                this.$stamp(null,result)
                this.$store.state.loading = false
                this.$message.success('骑行路线数据加载成功!')
              } else {
                this.$message.error('骑行路线数据查询失败!')
              }
          })
        }
      }).catch(error => {
        this.$message.error('网络错误,请重新刷新页面')
      })
    },
    methods:{
      //根据orderId查看骑手、店铺坐标
      getLoaction(orderId){
        return new Promise((resolve,reject) => {
          this.$api.orders.getLoaction({orderId}).then(res => {
            resolve(res)
          })
        })
      }
    },
    computed: {
      
    },
    components: {
      
    },
    created(){

    }
}
</script>

<style>
  #container {
      width: 100%;
      height: 100%;
  }
  #panel {
      position: fixed;
      background-color: white;
      max-height: 90%;
      overflow-y: auto;
      top: 10px;
      right: 10px;
      width: 280px;
  }
  #panel .amap-lib-driving {
      border-radius: 4px;
      overflow: hidden;
  }
</style>



